.box {
  height: 35px;
  width: 35px;
  min-height: 35px;
  min-width: 35px;
  margin: 6px;
  border: 1px solid steelblue;
  display: flex;
  justify-content: center;
  align-items: center;

  &:nth-child(odd) {
    background-color: #ddd;

    height: 45px;
    width: 45px;
    min-height: 45px;
    min-width: 45px;
  }
}
